export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "it": {
        "planSettingsTitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Dimensioni piano"])},
        "instruction": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Modifica la dimensione del piano di lavoro in base alle dimensioni della fioriera"])},
        "rows": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Righe"])},
        "columns": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Colonne"])},
        "clearDraw": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Cancella disegno"])},
        "reset": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Reset"])}
      }
    }
  })
}
